import React from "react";
import { Routes, Route, Navigate } from "react-router-dom";

import "./App.css";
import "./App.scss";

// importar las paginas
import Home from "./pages/Home";
import SignIn from "./pages/SignIn";
import SignUp from "./pages/SignUp";
import Terms from "./pages/Terms";
import Privacy from "./pages/Privacy";
import ForgotPassword from "./pages/ForgotPassword";
import { StyledEngineProvider } from "@mui/material/styles";
// import ProductDetail from "./pages/commerce/views/ProductDetail";

import PaperBase from "./pages/commerce/Paperbase";
import ShoppingCartProvider from "./pages/commerce/providers/ShoppingCartProvider";

// import { useLayoutEffect } from 'react'

// export const useBrowserLayoutEffect = process.browser ? useLayoutEffect : () => {}

function App() {
  return (
    <div className="app">
      {/* StyledEngineProvider injectFirst */}
      <Routes>
        <Route index element={<Home />} />
        <Route path="/sign-in/" element={<SignIn />} />
        <Route path="/sign-up/" element={<SignUp />} />
        <Route path="/terms/" element={<Terms />} />
        <Route path="/privacy/" element={<Privacy />} />
        <Route path="/forgot-password/" element={<ForgotPassword />} />
        <Route
          path="/tienda/*"
          element={
            <ShoppingCartProvider>
              <PaperBase />
            </ShoppingCartProvider>
          }
        />
        {/* <Route path="/producto/" element={<ProductDetail />} /> */}
        <Route path="*" element={<Navigate to="/"></Navigate>} />
      </Routes>
    </div>
  );
}

export default App;
